.greetingForm {
    display: flex;
    flex-direction: column;
}

.inputAndButtonContainer {
    display: flex;
    flex-direction: row;
    margin: 5px 0;
}

.error {
    color: #D1D1D1;
}

input {
    height: 35px; 
    width: 370px;   
    border-radius: 5px;    
    border-width: 1px; 
    padding-left: 10px;
}

.input {    
    border-color: rgba(0,0,0,.5);   
}

.input:focus {
}

.errorInput {
    border-color: red;
}

.button {
    margin-left: 5px;
    width: 70px;
    height: 35px;
    background-color: #0066CC;
    
    color: white;
    border-radius: 5px;
}

.button:focus {
}

.button:active {

}

.button:disabled {
    opacity: .5;
}

.text {
    color: rgba(0,0,0,.5);
    margin: 5px 0;
}

.greeting {
    margin: 5px 0;
}
